import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Empresa } from 'src/app/models/empresa';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EmpresaService {
  private apiUrl: string = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) {}

  public list() {
    return this.http.get<Empresa[]>(`${this.apiUrl}/empresas`);
  }

  public findById(id: number) {
    return this.http.get<Empresa>(`${this.apiUrl}/empresas/${id}`);
  }

  public create(empresa: Empresa): Observable<Empresa> {
    const data = new FormData();
    data.append('nome_empresa', empresa.nome_empresa);
    data.append('nome_fantasia', empresa.nome_fantasia);
    data.append('cnpj', empresa.cnpj);
    data.append('fone', empresa.fone);
    data.append('endereco', empresa.endereco);
    data.append('email', empresa.email);
    data.append('site', empresa.site);
    data.append('logo', empresa.logo);
    return this.http.post<Empresa>(`${this.apiUrl}/empresas`, data);
  }

  public update(empresa: Empresa): Observable<Empresa> {
    return this.http.put<Empresa>(`${this.apiUrl}/empresas/${empresa.id}`, empresa);
  }

  public updateLogo(id: number, logo: File): Observable<Empresa> {
    const data = new FormData();
    data.append('id', id.toString());
    data.append('logo', logo);
    return this.http.post<Empresa>(`${this.apiUrl}/empresas/logo`, data);
  }

  public delete(empresa: Empresa): Observable<Empresa> {
    return this.http.delete<Empresa>(`${this.apiUrl}/empresas/${empresa.id}`);
  }
}
